import React, { createContext, useContext, useState, useEffect } from 'react';
import axios from 'axios';

// Configuracin global de axios para las peticiones HTTP
axios.defaults.baseURL = 'https://vetcareclinica.com';
axios.defaults.headers.common['Content-Type'] = 'application/json';
axios.defaults.headers.common['Accept'] = 'application/json';
axios.defaults.withCredentials = true; // Necesario para el manejo de CORS

/**
 * Contexto de autenticacin para manejar el estado y las operaciones de autenticacin
 */
const AuthContext = createContext();

/**
 * Hook personalizado para acceder al contexto de autenticacin
 * @returns {Object} Contexto de autenticacin
 * @throws {Error} Si se usa fuera del AuthProvider
 */
export const useAuth = () => {
    const context = useContext(AuthContext);
    if (!context) {
        throw new Error('useAuth debe ser usado dentro de un AuthProvider');
    }
    return context;
};

/**
 * Proveedor del contexto de autenticacin
 * @param {Object} props - Propiedades del componente
 * @param {React.ReactNode} props.children - Componentes hijos
 */
export const AuthProvider = ({ children }) => {
    // Estados para manejar la autenticacin
    const [user, setUser] = useState(null);
    const [token, setToken] = useState(localStorage.getItem('token'));
    const [loading, setLoading] = useState(true);
    const [isAuthenticated, setIsAuthenticated] = useState(false);

    // Efecto para cargar el usuario cuando hay un token
    useEffect(() => {
        if (token) {
            axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
            fetchUser();
        } else {
            setLoading(false);
        }
    }, [token]);

    /**
     * Obtiene la informacin del usuario autenticado
     */
    const fetchUser = async () => {
        try {
            const response = await axios.get('/api/user');
            if (response.data) {
                setUser(response.data);
                setIsAuthenticated(true);
                // Guardar el ID del usuario en localStorage para uso posterior
                localStorage.setItem('userId', response.data.id_usuario);
            }
        } catch (error) {
            console.error('Error al obtener usuario:', error);
            localStorage.removeItem('token');
            localStorage.removeItem('userId');
            delete axios.defaults.headers.common['Authorization'];
            setToken(null);
            setUser(null);
            setIsAuthenticated(false);
        } finally {
            setLoading(false);
        }
    };

    /**
     * Inicia sesin con las credenciales proporcionadas
     * @param {Object} credentials - Credenciales de inicio de sesin
     * @returns {Object} Datos del usuario
     */
    const login = async (credentials) => {
        try {
            const response = await axios.post('/api/login', credentials);

            if (response.data && response.data.token) {
                const { token, usuario } = response.data;
                localStorage.setItem('token', token);
                localStorage.setItem('userId', usuario.id_usuario);
                setToken(token);
                setUser(usuario);
                setIsAuthenticated(true);
                axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
                return true;
            }
            return false;
        } catch (error) {
            console.error('Error en login:', error);
            throw error;
        }
    };

    /**
     * Registra un nuevo usuario
     * @param {Object} userData - Datos del usuario a registrar
     * @returns {Object} Datos del usuario registrado
     */
    const register = async (userData) => {
        try {
            const response = await axios.post('/api/register', userData);

            if (response.data && response.data.token) {
                const { token, usuario } = response.data;
                localStorage.setItem('token', token);
                localStorage.setItem('userId', usuario.id_usuario);
                setToken(token);
                axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
                setUser(usuario);
                setIsAuthenticated(true);
                return usuario;
            }
            throw new Error('Respuesta invlida del servidor');
        } catch (error) {
            throw new Error(error.response?.data?.message || 'Error en el registro');
        }
    };

    /**
     * Cierra la sesin del usuario actual
     */
    const logout = async () => {
        try {
            await axios.post('/api/logout');
        } catch (error) {
            console.error('Error al cerrar sesin:', error);
        } finally {
            localStorage.removeItem('token');
            localStorage.removeItem('userId');
            delete axios.defaults.headers.common['Authorization'];
            setToken(null);
            setUser(null);
            setIsAuthenticated(false);
        }
    };

    /**
     * Actualiza los datos del usuario
     * @param {Object} userData - Nuevos datos del usuario
     * @returns {Object} Datos actualizados del usuario
     */
    const updateUser = async (userData) => {
        try {
            const response = await axios.post('/api/user', userData);
            setUser(response.data);
            return response.data;
        } catch (error) {
            console.error('Error al actualizar usuario:', error);
            throw error;
        }
    };

    /**
     * Cambia la contrasea del usuario
     * @param {Object} passwordData - Datos para el cambio de contrasea
     * @returns {Object} Resultado de la operacin
     */
    const changePassword = async (passwordData) => {
        try {
            const response = await axios.post('/api/user/change-password', passwordData);
            return response.data;
        } catch (error) {
            console.error('Error al cambiar contrasea:', error);
            throw error;
        }
    };

    // Valor del contexto que se proporciona a los componentes hijos
    const value = {
        user,
        token,
        loading,
        isAuthenticated,
        login,
        register,
        logout,
        updateUser,
        changePassword
    };

    return (
        <AuthContext.Provider value={value}>
            {!loading && children}
        </AuthContext.Provider>
    );
}; 